.logo {
  display: flex;
  align-items: center;
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    height: 46px;
    border: 1px solid rgba(26, 56, 96, 0.1);
    border-radius: 4px;
  }
  &__icon {
    width: 30px;
    height: 30px;
    background: #3861FB;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    font-family: Helvetica,  sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 85.3%;
    color: #0C0C0C;
    margin: 0px 10px;
  }
}