@import '../../utils/breakpoints';

.navbar {
  margin-bottom: 30px;
  height: 55px;
  display: flex;
  align-items: center;
  border: 1px solid rgba(26, 56, 96, 0.1);
  border-top: none;
  border-left: none;
  border-right: none;
  justify-content: space-between;
  &__hamburger {
    margin-left: 4px;
    display: none;
  }
  &__title {
    font-family: Helvetica,  sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 85.3%;
    color: #0C0C0C;
    margin: 0px 10px;
    &-tail {
      font-family: Helvetica, sans-serif;
      font-weight: 400;
      margin: 0px 7px 0px 0px;
      color: rgba(26, 41, 61, 0.83);
    }
  }
  &__tail {
    text-decoration: none;
    display: flex;
    // margin: 0px 0px 0px auto;
  }
  &__user {
    height: 36px;
    display: flex;
    align-items: center;
    position: relative;
  }
  &__userMoreIcon {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    outline: none;
    border: none;
    background-color: white;
    &:hover {
      background: rgba(26, 56, 96, 0.1);
    }

  }
  &__userMore {
    display: none;
    width: 144px;
    position: absolute;
    z-index: 10;
    right: 0;
    top: 42px;
    padding: 12px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0px 0px 3px gray;
    &-active {
      display: block;
    }
  }
  &__userLink {
    font-family: Helvetica ;
    font-size: 16px;
    line-height: 125.3%;
    color: rgba(26, 41, 61, 0.83);
    margin: 4px 0px;
    text-decoration: none;
    display: block;
    &-logOut {
      color: red;
      margin-top: 20px;
    }
  }
  &__userName {
    margin: 0 8px;
    height: 20px;    
    font-family: Helvetica, sans-serif;
    font-size: 16px;
    line-height: 125.3%;
    color: rgba(26, 41, 61, 0.83);
    overflow: hidden;
    white-space: nowrap;
  }
  &__userImg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    background: #DF0B92;
    border-radius: 50%;
    font-family: Helvetica, sans-serif;
    font-size: 13px;
    line-height: 122%;
    color: white;
    text-transform: uppercase;
    text-overflow: clip;
  }

  &__center {
    height: 100%;
    margin: 0 auto;
    display: grid;
    align-items:center;
    column-gap: 30px;
    grid-template-columns: repeat(3, auto);
  }
  &__route {
    display: flex;
    align-items: center;
    height: 100%;
    align-self: middle;
    font-family: Helvetica, sans-serif;
    font-size: 16px;
    line-height: 125.3%;  
    color: rgba(26, 41, 61, 0.83);
    outline: none;
    border: none;
    background: white;
    &-active, &:hover {
      color: #1E4DFC;
      border-bottom: 1px solid #1E4DFC;
    }
  }

}


@media (max-width: $mobile-width) { 
  .navbar {
    border: none;
    padding-right: 20px;
    padding-left: 20px;
    &__userMoreIcon {
      display: none;
    }
    &__userName {
      display: none;
    }
    &__hamburger {
      display: block;
    }
    &__logoContainer {
      width: 40px;
      height: 40px;
    }
    &__title {
      font-size: 14px;
    }
    &__center {
      display: none;
    }
  }
}

@media (max-width: 450px) { 
  .navbar {
    margin-bottom: 36px;
  }
}