@keyframes alertAnimationOpacity {
  from {
    opacity: 0;
  }
  50% {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

.alert {
  display: flex;
  justify-content: center;
  align-items: center;
  animation: alertAnimationOpacity 2s ease-out forwards;
  height: 50px;
  margin-top: 5px;
  text-align: center;
  letter-spacing: 0.5px;
  border-radius: 5px;
  color: #f04c34;
}