@keyframes fromTopLeft {
  from {
    width: 150px;
    border-radius: 50%;
    height: 150px;
    visibility: hidden;
    opacity: 0;
  }
  to {
    width: 100%;
    height: 100%;
    border-radius: 0;
    opacity: 1;
  }
}

.menu {
  animation: fromTopLeft ease-in-out 0.5s forwards;
  position: fixed;
  z-index: 100;
  background: white;
  bottom: 0;
  top: 0;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;

  padding-top: 30px;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
  }
  &__title {
    font-size: 24px;
    line-height: 125.3%;
    color: #192434;
    margin-top: 36px;
  }
  &__links {
    margin-top: 26px;
    height: 590px;
    max-height: 590px;
    padding: 11px 16px;
  }
  &__link {
    display: block;
    font-family: Helvetica, sans-serif;
    font-size: 24px;
    line-height: 125.3%;
    color: rgba(26, 41, 61, 0.83);
    text-transform: capitalize;
    margin-bottom: 36px;
    &-inner {
      margin-bottom: 24px;
      margin-left: 16px;
    }
    &-last {
      margin-top: 36px;
    }
    &:hover {
      color: black;
    }
  }
  .border-gradient {
    border-left: 0.5px solid;
    border-right: 0.5px solid;
  }
}