@import '../../utils/breakpoints';

.container-auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 64px 0px 0px 0px;
  padding-bottom: 50px;
  padding-left: 20px;
  padding-right: 20px;
}

.auth {
  max-width: 500px;
  &__title {
    text-align: center;
    margin: 0px auto 0px auto;
    min-height: 50px;
    font-family: Inter, sans-serif;
    font-weight: bold;
    font-size: 40px;
    line-height: 125.3%;
    letter-spacing: -0.01em;
    color: #192434;
  }
  &__subtitle {
    text-align: center;
    margin: 8px auto 36px auto;
    min-height: 19px;
    font-family: Inter, sans-serif;
    font-size: 14px;
    line-height: 134%;
    letter-spacing: -0.01em;
    color: rgba(24, 39, 58, 0.94);
  }
  &__link {
    margin-left: 2px;
    color: #1E4DFC;
    text-decoration: none;
  }
  &__form { 
    margin-top: 36px;
  }
  &__input {
    &-password {
      letter-spacing: 5px;
      &::placeholder {
        letter-spacing: normal;
      }
    }
  }
  &__check {
    display: block;
    text-decoration: none;
    cursor: pointer;
    text-align: end;
    position: static;
    height: 19px;
    font-family: Inter, sans-serif;
    font-size: 14px;
    line-height: 134%;
    letter-spacing: -0.01em;
    color: rgba(27, 43, 65, 0.72);
    margin-top: 8px;
    margin-bottom: 40px;
    &-left {
      text-align: start;
      padding: 0 50px 0 0;
    }
  }
  &__btn {
    margin-top: 40px;
  }
  .password__icon {
    position: absolute;
    top: 32px;
    right: 12px;
    cursor: pointer;
  }
}

@media (max-width: $mobile-width) { 
  .container-auth {
    margin: 0;
  }
  .auth {
    margin: 0;
    &__title {
      font-size: 38px;
      line-height: 114.8%;
    }
    &__subtitle {
      margin-top: 16px;
    }
  }
}