@keyframes alertShow {
  from {
    height: 0px;
    opacity: 0;
  }
  to {
    height: 20px;
    opacity: 1;
  }
}

.field {
  position: relative;
  margin-top: 16px;
  &__input {
    width: 100%;
    display: block;
    height: 48px;
    border: 1px solid rgba(28, 55, 90, 0.16);
    box-sizing: border-box;
    border-radius: 4px;
    padding: 10px 11px;
    &:invalid {
      border: 1px dashed red;
    }
  }
  &__title {
    margin-bottom: 4px;
    height: 14px;
    font-family: Helvetica;
    font-size: 12px;
    line-height: 14px;
    color: rgba(24, 39, 58, 0.94);
  }
  &__alert {
    animation: alertShow .4s forwards;
    display: block;
    text-decoration: none;
    height: 19px;
    font-family: Inter, sans-serif;
    font-size: 12px;
    line-height: 134%;
    letter-spacing: -0.01em;
    color: rgba(218, 38, 6, 0.72);
  }
}
