@import './utils/breakpoints';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  &::-webkit-scrollbar {
    width: 12px;
    // background: rgb(243, 225, 225);
    border-radius: 2px;
  }
  
  &::-webkit-scrollbar-thumb {
    height: 16px;
    border-radius: 8px;
    border: 3px solid transparent;
    background-clip: content-box;
    background-color: #888;
    &:hover {
      background-color: #555;
    }
  }
}

html {
  overflow-y: scroll; // for not move elements when scrollbar appear
  overflow-y: overlay;
}

body {
  height: 100vh;
}

progress {
  -webkit-appearance: none;
}

input {
  outline: none;
}

button {
  border: 0;
  padding: 0;
  cursor: pointer;
  background: none;
}

h1 {
  font-weight: normal;
}

a {
  outline: none;
  text-decoration: none;
  color: white;
}

@keyframes openPageScaling {
  from {
    opacity: 0;
    transform: scale(0.25)
  }
  to {
    opacity: 1;
    transform: scale(1)
  }
}

@keyframes openPageTranslate {
  from {
    opacity: 0;
    transform: translateX(-50%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hidden {
  display: none !important;
}

.visible {
  display: block !important;
}

.border-gradient {
  border: double 2px transparent;
  border-radius: 4px;  
  border-image: linear-gradient(180deg, rgba(28, 52, 84, 0.12) 0%, rgba(28, 52, 84, 0) 115.28%) 1% 5%;
  border-bottom: none;
}

.btn {
  padding: 12px 54px;
  width: 100%;
  height: 48px;
  background: #1E4DFC;
  border: none;
  border-radius: 4px;
  text-align: center;
  color: white;
  cursor: pointer;
  &:disabled {
    background: rgba(28, 52, 84, 0.26);
  }
}


.container {
  padding: 28px 0px 30px 0px;
  max-width: 1256px;
  margin: 0 auto;
  min-height: 100%;
}

@media (max-width: 1200px) { 
  .container {
    padding: 28px 40px 30px 40px;
  }
}

@media (max-width: $mobile-width) { 
  .container {
    padding-left: 0;    
    padding-right: 0;
  }
  .border-gradient {
    border-left: none;
    border-right: none;
  }
}